@import url(https://fonts.googleapis.com/css2?family=Public+Sans:wght@300;400;700&display=swap);

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    font-family: "Public Sans", sans-serif;
}

p {
    color: hsl(233, 26%, 24%);
}
.article {
    background-color: white;
}

.atricle__header {
    height: 200px;
    max-width: 100%;
    margin-bottom: 1rem;
}

.atricle__header img {
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.article__author {
    font-size: 0.6rem;
    padding-bottom: 0.5rem;
}

.article__content>h5 {
    font-size: 1rem;
    font-weight: 400;
    padding-bottom: 0.5rem;
}

.article__content>h5:hover {
    cursor: pointer;
    color: hsl(136, 65%, 51%);
}

.article__content {
    padding: 1rem;
}

.latestArticle__text {
    line-height: 1rem;
    font-weight: 500;
    font-size: 0.8rem;
    color: hsl(233, 8%, 62%);
    padding-bottom: 0.5rem;
}
.articles-section {
    height: 100vh;
    width: 100%;
    background-color: hsl(0, 0%, 98%);
    display: flex;
    align-items: center;
    justify-content: center;
}

.articles-section__content {
    width: 1440px;
    max-width: 90%;
    height: 80%;
}

.articles-section__content>h2 {
    font-size: 2.1rem;
    font-weight: 400;
    padding-bottom: 2rem;
}

.ASCard_wrapper {
    width: 100%;
    display: flex;
    justify-content: space-between;
    grid-gap: 2rem;
    gap: 2rem;
}

@media (max-width: 748px) {
    .ASCard_wrapper {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
    .articles-section__content>h2 {
        text-align: center;
    }
    .articles-section {
        height: auto;
        padding-bottom: 3rem;
        padding-top: 3rem;
    }
}

@media (max-width: 468px) {
    .ASCard_wrapper {
        display: grid;
        grid-template-columns: 1fr;
    }
}
.benefitCard>* {
    padding-bottom: 2rem;
    line-height: 1.5rem;
}

.benefitCard>h3 {
    font-weight: 400;
}

.benefitCard>p {
    color: hsl(233, 8%, 62%);
}

@media (max-width: 748px) {
    .benefitCard {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
    }
}
.benefits {
    background-color: hsl(220, 16%, 96%);
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
}

.benefits__content {
    height: 70%;
    width: 1440px;
    max-width: 90%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.benefits__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
    height: 50%;
}

.benefits__header>h2 {
    font-size: 2.1rem;
    font-weight: 400;
    padding-bottom: 2rem;
}

@media (max-width: 748px) {
    .benefits__grid {
        grid-template-columns: 1fr 1fr;
    }
    .benefits {
        padding: 3rem 0;
        height: auto;
    }
    .benefits__header {
        padding-bottom: 4rem;
        text-align: center;
    }
}

@media (max-width: 468px) {
    .benefits__grid {
        grid-template-columns: 1fr;
    }
}
.footer {
    width: 100%;
    height: 100px;
    background-color: hsl(233, 26%, 24%);
    padding: 1rem 0;
}

.footer__content {
    width: 1440px;
    max-width: 90%;
    height: 100%;
    display: grid;
    grid-template-columns: 4fr 2fr 2fr 5fr;
    margin: 0 auto;
}

.footer__icons {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    color: white;
    padding-right: 1rem;
}

.footer__icons>span>* {
    padding-left: 0.5rem;
    cursor: pointer;
}

.footer__menu>ul,
.footer__menu2>ul {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
}

.footer__menu li,
.footer__menu2 li {
    padding: 0.2rem;
    font-size: 14px;
    font-weight: 200;
}

.footer__menu a,
.footer__menu2 a {
    text-decoration: none;
    color: white;
}

.footer_copyright {
    display: inline-flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-between;
}

.footer_copyright>p {
    color: hsl(233, 8%, 62%);
    font-size: 12px;
}

@media (max-width: 748px) {
    .footer {
        height: auto;
    }
    .footer__content {
        height: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 1rem 0;
    }
    .footer__menu>ul,
    .footer__menu2>ul {
        align-items: center;
        justify-content: space-between;
    }
    .footer__icons>img {
        padding-bottom: 1rem;
    }
    .footer__menu {
        padding-top: 1rem;
    }
    .footer__menu2 {
        padding-bottom: 1rem;
    }
    .footer_copyright {
        align-items: center;
    }
    .footer_copyright>p {
        padding-top: 1rem;
    }
    .footer__menu li,
    .footer__menu2 li {
        padding: 0.5rem;
        font-size: 14px;
        font-weight: 200;
    }
}
.btn {
    padding: 0.8rem 2rem;
    color: hsl(0, 0%, 100%);
    outline: none;
    border: none;
    font-size: 14px;
    font-weight: 600;
    border-radius: 35px;
    background: linear-gradient(to right, hsl(136, 65%, 31%), hsl(192, 70%, 51%));
}

.btn:hover {
    cursor: pointer;
    background: linear-gradient(to right, hsl(136, 65%, 45%), hsl(192, 70%, 65%));
}
.hero {
    width: 1440px;
    max-width: 90%;
    height: 100vh;
    display: grid;
    grid-template-columns: 40% 60%;
    margin: 0 auto;
}

.hero__left {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.hero__left>h1 {
    font-weight: 500;
}

.hero__left>* {
    padding-bottom: 1rem;
}

.hero__right {
    position: relative;
    height: 100%;
    margin-top: auto;
}

.hero-bg-img {
    width: 100%;
    position: absolute;
    top: 0;
}

.hero-bg-mockup {
    width: 100%;
    position: absolute;
    top: 0;
    right: 0;
}

@media (max-width: 748px) {
    .hero {
        display: flex;
        flex-direction: column-reverse;
    }
    .hero__right {
        height: 70%;
    }
    .hero-bg-mockup {
        width: 70%;
        margin-left: auto;
        margin-right: auto;
        left: 0;
        right: 0;
        text-align: center;
    }
    .hero__left {
        height: 70%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
}

@media (max-width: 536px) {
    .hero__left {
        justify-content: flex-start;
    }
    .hero__left h1,
    p {
        text-align: center;
    }
    .hero__left p {
        line-height: 1.6rem;
    }
}


