    .hero-bg-mockup {
        width: 90%;
    }
} */
.navbar {
    background-color: hsl(0, 0%, 100%);
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: center;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 100;
}

nav {
    height: 100%;
    width: 1440px;
    max-width: 90%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
}

.nav-list {
    display: flex;
    list-style: none;
    align-items: center;
    height: 80px;
}

.nav-list li {
    padding-left: 1rem;
    display: flex;
    align-items: center;
    text-align: center;
    height: 100%;
}

.nav-list li a {
    text-decoration: none;
    color: hsl(233, 26%, 24%);
    height: 100%;
    display: flex;
    align-items: center;
    border-bottom: 3px solid transparent;
}

.nav-list li a:hover {
    border-bottom: 3px solid hsl(136, 65%, 51%);
    color: hsl(233, 26%, 0%);
}

.menu-icon {
    position: absolute;
    top: 30px;
    right: 20px;
    display: none !important;
}

@media (max-width: 740px) {
    .menu-icon {
        display: block !important;
    }
    .menu-button {
        display: none;
    }
    .nav-list {
        display: none;
    }
    .nav-list-active {
        display: flex;
        background-color: white;
        box-shadow: 0px 0px 50px 40px rgba(0, 0, 0, 0.44);
        border-radius: 8px;
        flex-direction: column;
        font-size: 18px;
        width: 80%;
        height: auto;
        padding: 2rem 0;
        position: absolute;
        margin-left: auto;
        margin-right: auto;
        left: 0;
        right: 0;
        top: 100px;
        text-align: center;
    }
    .nav-list li {
        padding: 0.8rem;
    }
}
